<template>
  <div class="row_one border level_list">
    <div style="height:170px;">
      <img class="fit_img" :src="project.project_img">
    </div>
    <div class="h_justify padding_15">
      <Star :score="parseFloat(project.total_score)" :width="24"></Star>
      <div class="orange">
        <span class="font_bold" style="font-size:36px">{{ parseFloat(project.total_score).toFixed(1) }}</span>
        <span class="font_16">分</span>
      </div>
    </div>
    <div class="light padding_h_15 margin_b_15 lineheight_2">
      <div>ICO时间：{{ project.ico_start_time+'-'+project.ico_end_time }}</div>
      <div>ICO金额：{{ project.ico_amount }}元</div>
    </div>
    <router-link :to="'/Detail/' + project.id + '/Introduction'" :class="['block btn_block btn_blue lineheight_3']" style="border-radius:0">查看详情</router-link>
  </div>
</template>

<script>
import Star from '@/components/Parts/Star'
export default {
  name: 'project',
  props: {
    project: {
      type: Object
    }
  },
  components: {
    Star
  }
}
</script>

<style type="text/css">
  .level_list.row_one{
    position: relative;
    margin-bottom: 30px;
    box-shadow: 0 4px 14px 0 rgba(0,0,0,.1) 
  }
  .level_list.row_one:hover {
    top: -5px;
  }
</style>



